const BASE_API = `http://localhost:5000/`

const columns = [
    {field: 'family', headerName: '', width: 90},
    {
        field: 'name',
        headerName: '',
        width: 150,
    },
    {
        field: 'surname',
        headerName: '',
        width: 150,
    },
    {
        field: 'sex',
        headerName: '',
        width: 150,
        valueGetter: (params) => {
            if(params === 'female') return ''
            if(params === 'male') return ''
        }
    },
    {
        field: 'country',
        headerName: '',
        width: 150,
        valueGetter: (params) => {
            return params?.label || ''
        }
    },
    {
        field: 'age',
        headerName: '',
        width: 150,
    },
    {
        field: 'traditional',
        headerName: '',
        width: 400,
        valueGetter:(value, row) => {
            let result = ''
            if (row?.dietician) result += '\n'
            if (row?.vegan) result += '\n'
            if (row?.traditional) result += ''
            return result
        }
    },
]

